.arrow-group {
  position: relative;
  display: inline-block;
  top: 2px; }
  .arrow-group .arrow {
    display: block;
    margin-top: .3em;
    margin-left: .3em;
    opacity: .5;
    cursor: pointer;
    height: 0;
    width: 0;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent; }
    .arrow-group .arrow.active {
      opacity: 1;
      color: #c7cce6; }
    .arrow-group .arrow-up {
      border-bottom: 4px solid #717a9a; }
    .arrow-group .arrow-down {
      border-top: 4px solid #717a9a; }

.table-wrapper {
  position: relative;
  overflow: hidden;
  background-color: #fff;
  color: #606266;
  border: 1px solid #ebeef5; }
  .table-wrapper.no-border {
    border: none;
    border-bottom: none; }

.qb-table-header-wrapper table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate; }
  .qb-table-header-wrapper table th {
    padding: 12px 0;
    min-width: 0;
    box-sizing: border-box;
    text-overflow: ellipsis;
    vertical-align: middle;
    position: relative;
    text-align: left;
    border-bottom: 1px solid #ebeef5;
    border-right: 1px solid #ebeef5; }
    .qb-table-header-wrapper table th.no-border {
      border-right: none;
      border-bottom: none; }
    .qb-table-header-wrapper table th .sortable {
      cursor: pointer;
      user-select: none; }
    .qb-table-header-wrapper table th .cell {
      padding: 0 10px;
      font-size: 13px; }
    .qb-table-header-wrapper table th.is-left {
      text-align: left; }
    .qb-table-header-wrapper table th.is-center {
      text-align: center; }
    .qb-table-header-wrapper table th.is-right {
      text-align: right; }

.qb-table-body-wrapper table {
  width: 100%;
  table-layout: fixed;
  border-collapse: separate; }
  .qb-table-body-wrapper table td {
    padding: 12px 0;
    min-width: 0;
    box-sizing: border-box;
    text-overflow: ellipsis;
    white-space: normal;
    vertical-align: middle;
    position: relative;
    text-align: left;
    border-bottom: 1px solid #ebeef5;
    border-right: 1px solid #ebeef5; }
    .qb-table-body-wrapper table td.is-left {
      text-align: left; }
    .qb-table-body-wrapper table td.is-center {
      text-align: center; }
    .qb-table-body-wrapper table td.is-right {
      text-align: right; }
    .qb-table-body-wrapper table td.no-border {
      border-right: none;
      border-bottom: none; }
    .qb-table-body-wrapper table td .cell {
      padding: 0 10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 13px; }

.qb-table-body-wrapper.hover-highlight tr:hover {
  background-color: #f5f7fa;
  transition: background-color 0.3s ease-out; }

.black .qb-table-header-wrapper, .black .qb-table-body-wrapper {
  background: #131723; }
  .black .qb-table-header-wrapper .check-box, .black .qb-table-body-wrapper .check-box {
    display: inline-block; }
    .black .qb-table-header-wrapper .check-box input[type="checkbox"], .black .qb-table-body-wrapper .check-box input[type="checkbox"] {
      position: absolute;
      margin: 0;
      margin-top: 6px;
      z-index: -1;
      opacity: 0; }
    .black .qb-table-header-wrapper .check-box input[type="checkbox"]:checked ~ .indicator, .black .qb-table-body-wrapper .check-box input[type="checkbox"]:checked ~ .indicator {
      background-image: url(https://cdn.1tokentrade.cn/otimg-sh/quant/list/checked.png); }
    .black .qb-table-header-wrapper .check-box input[type="checkbox"]:disabled ~ .indicator, .black .qb-table-body-wrapper .check-box input[type="checkbox"]:disabled ~ .indicator {
      cursor: default; }
    .black .qb-table-header-wrapper .check-box .indicator, .black .qb-table-body-wrapper .check-box .indicator {
      display: inline-block;
      width: 14px;
      height: 14px;
      background: url(https://cdn.1tokentrade.cn/otimg-sh/quant/list/uncheck.png) no-repeat 50%;
      background-size: cover;
      vertical-align: -3px;
      cursor: pointer; }
  .black .qb-table-header-wrapper.hover-highlight tr:hover, .black .qb-table-body-wrapper.hover-highlight tr:hover {
    background-color: #252b3b;
    transition: background-color 0.3s ease-out; }

.black .qb-table-header-wrapper th, .black .qb-table-header-wrapper td {
  color: #5f6b7f;
  font-size: 14px;
  letter-spacing: 1px; }

.black .qb-table-body-wrapper th, .black .qb-table-body-wrapper td {
  font-size: 14px;
  color: #a1afc3; }

.black .qb-table-body-wrapper tr.odd {
  background-color: #1c212d; }

.black .qb-table-body-wrapper tr.even {
  background-color: #121723; }
